/* Global Variables */
:root {
  /* Tech Theme - Flat Professional Palette */
  --primary-color: #1371f6;      /* Deep tech navy */
  --primary-dark: #0f1419;       /* Darker navy for hovers */
  --primary-light: #2d3748;      /* Lighter tech gray */
  --secondary-color: #4a5568;     /* Tech gray */
  --accent-color: #00d4ff;       /* Electric cyan - tech highlight */
  --accent-warm: #ff6b35;        /* Tech orange for contrast */
  --accent-green: #00f5a0;       /* Terminal green */
  --success-color: #48bb78;      /* Flat green */
  --warning-color: #ed8936;      /* Flat orange */
  --error-color: #f56565;        /* Flat red */

  /* Light Theme Neutral Colors */
  --white: #ffffff;
  --gray-50: #f8fafc;            /* Lightest background */
  --gray-100: #f1f5f9;          /* Card backgrounds */
  --gray-200: #e2e8f0;          /* Borders light */
  --gray-300: #cbd5e1;          /* Borders medium */
  --gray-400: #94a3b8;          /* Text muted */
  --gray-500: #64748b;          /* Text secondary */
  --gray-600: #475569;          /* Text primary light */
  --gray-700: #334155;          /* Headings light */
  --gray-800: #1e293b;          /* Dark text */
  --gray-900: #0f172a;          /* Darkest text */

  /* Additional professional tones */
  --slate-50: #f8fafc;
  --slate-100: #f1f5f9;
  --slate-200: #e2e8f0;
  --slate-300: #cbd5e1;
  --slate-400: #94a3b8;
  --slate-500: #64748b;
  --slate-600: #475569;
  --slate-700: #334155;
  --slate-800: #1e293b;
  --slate-900: #0f172a;

  /* Typography */
  --font-family-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  --font-family-mono: 'Fira Code', 'Monaco', 'Cascadia Code', 'Segoe UI Mono', monospace;

  /* Font Sizes */
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-lg: 1.125rem;
  --text-xl: 1.25rem;
  --text-2xl: 1.5rem;
  --text-3xl: 1.875rem;
  --text-4xl: 2.25rem;
  --text-5xl: 3rem;
  --text-6xl: 3.75rem;

  /* Spacing */
  --spacing-1: 0.25rem;
  --spacing-2: 0.5rem;
  --spacing-3: 0.75rem;
  --spacing-4: 1rem;
  --spacing-5: 1.25rem;
  --spacing-6: 1.5rem;
  --spacing-8: 2rem;
  --spacing-10: 2.5rem;
  --spacing-12: 3rem;
  --spacing-16: 4rem;
  --spacing-20: 5rem;
  --spacing-24: 6rem;

  /* Border Radius */
  --radius-sm: 0.125rem;
  --radius-base: 0.25rem;
  --radius-md: 0.375rem;
  --radius-lg: 0.5rem;
  --radius-xl: 0.75rem;
  --radius-2xl: 1rem;
  --radius-full: 9999px;

  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow-base: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);

  /* Transitions */
  --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-base: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 500ms cubic-bezier(0.4, 0, 0.2, 1);

  /* Container Widths */
  --container-sm: 640px;
  --container-md: 768px;
  --container-lg: 1024px;
  --container-xl: 1280px;
  --container-2xl: 1536px;
}

/* Dark Mode Variables - Minimalistic Professional Theme */
[data-theme="dark"] {
  /* Refined minimal color palette - professional and sophisticated */
  --primary-color: #5792e5;      /* Modern indigo for primary actions */
  --primary-dark: #2f65d8;       /* Deeper indigo for hovers */
  --primary-light: #8b5cf6;      /* Lighter purple for accents */
  --accent-color: #06b6d4;       /* Professional cyan - more muted */
  --accent-warm: #f59e0b;        /* Subtle amber for highlights */
  --accent-green: #10b981;       /* Muted emerald green */

  /* Sophisticated dark background system - higher contrast for readability */
  --white: #0a0a0b;              /* Deep charcoal background */
  --gray-50: #161618;            /* Card/panel backgrounds */
  --gray-100: #1f1f23;          /* Elevated surfaces */
  --gray-200: #27272a;          /* Interactive elements */
  --gray-300: #3f3f46;          /* Borders and dividers */
  --gray-400: #71717a;          /* Muted text - better contrast */
  --gray-500: #a1a1aa;          /* Secondary text */
  --gray-600: #d4d4d8;          /* Primary text */
  --gray-700: #e4e4e7;          /* Headings */
  --gray-800: #f4f4f5;          /* High contrast text */
  --gray-900: #ffffff;          /* Maximum contrast */

  /* Consistent surface system for professional look */
  --slate-50: #0f0f11;          /* Primary dark background */
  --slate-100: #18181b;         /* Secondary backgrounds */
  --slate-200: #27272a;         /* Card surfaces */
  --slate-300: #3f3f46;         /* Interactive surfaces */
  --slate-400: #52525b;         /* Subtle borders */
  --slate-500: #71717a;         /* Text muted */
  --slate-600: #a1a1aa;         /* Text secondary */
  --slate-700: #d4d4d8;         /* Text primary */
  --slate-800: #e4e4e7;         /* Headings */
  --slate-900: #f8fafc;         /* High contrast */

  /* Professional surface system - minimal and clean */
  --surface-primary: #18181b;    /* Primary surface color */
  --surface-secondary: #27272a;  /* Secondary surface color */
  --surface-elevated: #3f3f46;   /* Elevated/hover surfaces - more subtle */
  --border-subtle: #27272a;      /* Very subtle borders */
  --border-default: #3f3f46;     /* Default borders - more defined */
  
  /* Professional shadow system for depth */
  --shadow-dark-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  --shadow-dark-md: 0 4px 6px -1px rgba(0, 0, 0, 0.3), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
  --shadow-dark-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.4), 0 4px 6px -2px rgba(0, 0, 0, 0.2);
  --shadow-dark-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.5), 0 10px 10px -5px rgba(0, 0, 0, 0.3);
}

/* Reset and Base Styles */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

body {
  font-family: var(--font-family-primary);
  font-size: var(--text-base);
  line-height: 1.6;
  color: var(--gray-700);
  background-color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.2;
  color: var(--gray-900);
  margin-bottom: var(--spacing-4);
}

h1 {
  font-size: var(--text-4xl);
}

h2 {
  font-size: var(--text-3xl);
}

h3 {
  font-size: var(--text-2xl);
}

h4 {
  font-size: var(--text-xl);
}

h5 {
  font-size: var(--text-lg);
}

h6 {
  font-size: var(--text-base);
}

p {
  margin-bottom: var(--spacing-4);
}

a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color var(--transition-fast);
}

a:hover {
  color: var(--primary-dark);
}

/* Layout */
.container {
  width: 100%;
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: 0 var(--spacing-4);
}

.section {
  padding: var(--spacing-12) 0;
}

.section-title {
  text-align: center;
  margin-bottom: var(--spacing-16);
  position: relative;
}

.section-title::after {
  content: '';
  width: 60px;
  height: 4px;
  background: var(--accent-color);
  border-radius: var(--radius-full);
  position: absolute;
  bottom: -var(--spacing-3);
  left: 50%;
  transform: translateX(-50%);
}

/* Flat tech design elements */
.tech-primary {
  background: var(--primary-color);
  color: var(--white);
}

.tech-accent {
  background: var(--accent-color);
  color: var(--primary-color);
}

.tech-text-highlight {
  color: var(--accent-color);
  font-weight: 600;
}

/* Tech-inspired flat borders */
.tech-border {
  border: 2px solid var(--accent-color);
}

.tech-border-subtle {
  border: 1px solid var(--border-default);
}

/* Minimal glow effects - very subtle */
.tech-glow {
  box-shadow: 0 0 15px rgba(99, 102, 241, 0.08);
}

[data-theme="dark"] .tech-glow {
  box-shadow: 0 0 20px rgba(99, 102, 241, 0.12);
}

/* Professional card styling */
.tech-card {
  border: 1px solid var(--border-default);
  border-radius: var(--radius-lg);
  background: var(--white);
  transition: all var(--transition-base);
}

.tech-card:hover {
  border-color: var(--gray-300);
  box-shadow: var(--shadow-md);
}

[data-theme="dark"] .tech-card {
  background: var(--surface-secondary);
  border-color: var(--border-subtle);
  box-shadow: var(--shadow-dark-sm);
}

[data-theme="dark"] .tech-card:hover {
  background: var(--surface-elevated);
  border-color: var(--border-default);
  box-shadow: var(--shadow-dark-md);
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-2);
  padding: var(--spacing-3) var(--spacing-6);
  font-size: var(--text-base);
  font-weight: 500;
  text-decoration: none;
  border: none;
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all var(--transition-fast);
  white-space: nowrap;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--white);
}

.btn-primary:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-secondary {
  background-color: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.btn-secondary:hover {
  background-color: var(--primary-color);
  color: var(--white);
  transform: translateY(-2px);
}

/* Professional button styles for dark mode */
[data-theme="dark"] .btn-primary {
  background: var(--primary-color);
  box-shadow: var(--shadow-dark-sm);
  border: 2px solid var(--primary-color);
}

[data-theme="dark"] .btn-primary:hover {
  background: var(--primary-dark);
  border-color: var(--primary-dark);
  box-shadow: var(--shadow-dark-md);
  color: var(--white);
}

[data-theme="dark"] .btn-secondary {
  background-color: transparent;
  border-color: var(--primary-color);
  color: var(--primary-color);
}

[data-theme="dark"] .btn-secondary:hover {
  background: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--white);
}

.btn-lg {
  padding: var(--spacing-4) var(--spacing-8);
  font-size: var(--text-lg);
}

/* Cards - Professional minimal design */
.card {
  background: var(--white);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  padding: var(--spacing-6);
  transition: all var(--transition-base);
  border: 1px solid var(--gray-200);
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-xl);
  border-color: var(--gray-300);
}

/* Professional dark mode card styling */
[data-theme="dark"] .card {
  background: var(--surface-secondary);
  border-color: var(--border-subtle);
  box-shadow: var(--shadow-dark-sm);
}

[data-theme="dark"] .card:hover {
  background: var(--surface-elevated);
  border-color: var(--border-default);
  box-shadow: var(--shadow-dark-lg);
  transform: translateY(-2px);
}

/* Responsive Grid */
.grid {
  display: grid;
  gap: var(--spacing-6);
}

.grid-cols-1 {
  grid-template-columns: 1fr;
}

.grid-cols-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-cols-3 {
  grid-template-columns: repeat(3, 1fr);
}

/* Responsive Design */
@media (max-width: 768px) {
  :root {
    --text-4xl: 2rem;
    --text-3xl: 1.5rem;
    --text-2xl: 1.25rem;
    --spacing-20: 3rem;
    --spacing-16: 2rem;
  }

  .container {
    padding: 0 var(--spacing-3);
  }

  .grid-cols-2, 
  .grid-cols-3 {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 768px) {
  .md\\:grid-cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .md\\:grid-cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1024px) {
  .lg\\:grid-cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .lg\\:grid-cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .lg\\:grid-cols-4 {
    grid-template-columns: repeat(4, 1fr);
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.6s ease-out;
}

.animate-fade-in-left {
  animation: fadeInLeft 0.6s ease-out;
}

.animate-fade-in-right {
  animation: fadeInRight 0.6s ease-out;
}

/* Loading States */
.skeleton {
  background: linear-gradient(90deg, var(--gray-200) 25%, var(--gray-100) 50%, var(--gray-200) 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Focus Styles */
*:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

button:focus,
a:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
} 